<template>
  <view class="container">
    <u-form class="userInfo" :model="form" ref="loginForm">
      <u-form-item  prop="username">
        <u-input v-model="form.username" placeholder="请输入用户名" />
      </u-form-item>
      <u-form-item  prop="password">
        <u-input v-model="form.password" placeholder="请输入密码" password />
      </u-form-item>
      <u-button style="text-align: ;" type="primary" @click="handleLogin">登录</u-button>
      <u-button type="default" @click="goToRegister">去注册</u-button>
      <u-button type="text" @click="goToForgotPassword">忘记密码</u-button>
    </u-form>
  </view>
</template>

<script>
export default {
  data() {
    return {
      form: {
        username: "",
        password: "",
      },
    };
  },
  methods: {
    // 处理登录
    handleLogin() {
      if (!this.form.username || !this.form.password) {
        uni.showToast({
          title: "用户名和密码不能为空",
          icon: "none",
        });
        return;
      }
      // 模拟登录成功
      uni.showToast({
        title: "登录成功",
        icon: "success",
      });
      uni.switchTab({
        url: "/pages/index/index",
      });
    },
    // 跳转到注册页面
    goToRegister() {
      uni.navigateTo({
        url: "/pages/login/register",
      });
    },
    // 跳转到找回密码页面
    goToForgotPassword() {
      uni.navigateTo({
        url: "/pages/login/forgot-password",
      });
    },
  },
};
</script>

<style>
.container {
  padding:70px 40px ;
}

</style>